<template>
	<div class="TransferAccounts">
		<mu-list style="background: white;margin: 0;padding: 0" @click="openBotttomSheet">
			<mu-list-item button>
				<mu-list-item-title>{{typeName}}</mu-list-item-title>
				<mu-list-item-action>
					<i class="icon iconfont">&#xe695;</i>
				</mu-list-item-action>
			</mu-list-item>
		</mu-list>
		
		<div class="sub-header">
			钱包余额 ：{{Number(count).toFixed(8)}}{{typeName}}
		</div>

		<div style="background: white;">
			<div style="padding:0 30px;">
				<!--<mu-text-field v-model="address" label="地址：" full-width ></mu-text-field>-->
				<mu-text-field style="width: 100%;" v-model="address" type="text" label="地址：" placeholder="请输入收款地址" action-icon=":iconfont icon-saoyisaoerweimasaomasaomiao1" :action-click="startRecognize"></mu-text-field>
			</div>
			<div style="padding:0 30px;">
				<mu-text-field v-model="money" label="金额：" full-width placeholder="请输入转账金额"></mu-text-field>
			</div>
			<div style="padding:0 30px;">
				<mu-text-field v-model="password" type="password" label="交易密码：" full-width placeholder="请输入交易密码"></mu-text-field>
			</div>
			<!--<div style="padding:0 30px;">
				<mu-text-field v-model="serviceCharge" label="手续费：" full-width placeholder="0.0000000ETH"></mu-text-field>
			</div>-->
			<div style="padding:10px 30px">
				<mu-button full-width style="height: 44px;" color="#295DF1" @click="confirm">确认转账</mu-button>
				<!--<div style="margin-top: 10px;">
					<router-link to="/transferAccountsRecord">
						<mu-button full-width class="importBtn" color="#CEBC98">转账记录</mu-button>
					</router-link>
				</div>-->
			</div>
		</div>

		<div style="padding:0px 20px;color: red;margin-bottom: 100px;">
			<h3 style="margin-bottom: 0;">提示：</h3>
			<p>转账前请务必确认地址及币种信息无误，请勿直接提现至众筹或ICO地址。一旦转出，不可撤回！</p>
		</div>
		<mu-bottom-sheet :open.sync="open">
			<mu-list @item-click="closeBottomSheet" style="margin: 0;padding: 0;">
				<mu-list-item v-for="item,index in coinsList" @click="chooseType(item,index)" :key="index" style="border-bottom: 1px solid #f5f5f5;" button>
					<mu-list-item-title style="text-align: center;">{{item}}</mu-list-item-title>
				</mu-list-item>
			</mu-list>
		</mu-bottom-sheet>

	</div>
</template>

<script>
	import '@/assets/font/iconfont.css'
	import $ from "jquery"
	import Tx from "ethereumjs-tx";
	let scan = null;
	let web3 = null;
	export default {
		name: 'TransferAccounts',
		data() {
			return {
				address: '',
				money: '',
				password: '',
				serviceCharge: '',
				open: false,
				typeName: '',
				type: '',
				count: 0,
				showSaoYiSao: false,
				chooseWallet:{},
				coinsList:['ETH','DYLC','OMG','BTM']

			}
		},
		mounted() {
			this.typeName = this.$store.state.homeDetailsTitle?this.$store.state.homeDetailsTitle:this.coinsList[0];
			web3 = new Web3(new Web3.providers.HttpProvider("https://mainnet.infura.io/v3/b7f93674854b43fea9d265249ba24c2d"));
		},
		methods: {
			confirm(){
				if(this.typeName == 'ETH'){
					this.sendEth();
				}else{
					this.sendToken();
				}
			},
			sendEth() {
				let _this = this;
				if(_this.password != _this.chooseWallet.pwd){
					_this.$layer.open({
						content: '交易密码输入错误',
						skin: 'msg',
						time: 2
					});
					return;
				}
		      let account = "0xe42154569f781a4b400ab9f536d42d209b2bfe3d";
//			  let account = '_this.chooseWallet.address';
		      let privateKey = _this.chooseWallet.privateKey;
		      let to = _this.address;
		      // 获取ETH余额
		      let Balance = web3.eth.getBalance(account);
		      console.log(web3.fromWei(Balance, "ether").toNumber());
		      // 获取账户交易量
		      var privateKeyBuffer = new Buffer.from(privateKey, "hex");
		      var rawTx = {
		        nonce: web3.toHex(web3.eth.getTransactionCount(account)),
		        gasPrice: web3.toHex(web3.toWei(20, "gwei")), // web3.toHex(web3.toWei(20, "gwei"))
		        gasLimit: web3.toHex(30400), // web3.toHex(30400)
		        to: to,
		        value: web3.toHex(web3.toWei(_this.money, "ether")), // ether 18
		        data: web3.toHex("")
		      };
		      console.log(rawTx);
		
		      var tx = new Tx(rawTx);
		      tx.sign(privateKeyBuffer);
		      var serializedTx = tx.serialize();
		      console.log("serializedTx.toString('hex')",serializedTx.toString('hex'));
		      web3.eth.sendRawTransaction("0x" + serializedTx.toString("hex"), function(
		        err,
		        hash
		      ) {
		        if (!err) {
		          console.log(hash);
		          // 查询交易状态 可加入任务,定时查询推送成功状态
		          web3.eth.getTransaction(hash, function(
		            blockNumber,
		            indexOfTransaction
		          ) {
		            //console.log(blockNumber);
		            //console.log(indexOfTransaction); // 交易详情 blockHash/blockNumber 交易完成
		          });
		        } else {
		          console.log(err);
		        }
		      });
		    },
			back: function() {
				this.$router.back();
			},
			closeBottomSheet() {
				this.open = false;
			},
			openBotttomSheet() {
				this.open = true;
			},
			chooseType(typeName, type) {
				this.typeName = typeName;
				this.type = type;
//				this.$store.commit('setTitle',typeName)
//				this.$store.commit('setType',type)
				//切换币种显示对应的余额
				if(typeName == 'ETH'){
					this.count = web3.eth.getBalance(this.chooseWallet.address);
				}else{
			      // // 合约地址
			      let contractAddress = "0x888389733a2d35fc86d3b5dc10dba34d0fff5868";
			      // // 定义合约
			      let VotingContract = web3.eth.contract(this.$lib.abiobj);
			      // 合约对象
			      let contractInstance = VotingContract.at(contractAddress);
			      // console.log(contractInstance);
					//代币余额
			      this.count = contractInstance.balanceOf(this.chooseWallet.address);
				}
			},
//			confirm() {
//				let _this = this;
//				let data = {
//					pay: _this.password,
//					num: _this.money,
//					address: _this.address,
//					type: _this.type,
//					id: localStorage.getItem('walletId') ? localStorage.getItem('walletId') : _this.$lib.walletList[0].id
//				}
//				if(_this.address == '') {
//					_this.$layer.open({
//						content: '请填写收款地址',
//						skin: 'msg',
//						time: 2
//					});
//					return;
//				}
//				if(_this.money == '') {
//					_this.$layer.open({
//						content: '请填写转账金额',
//						skin: 'msg',
//						time: 2
//					});
//					return;
//				}
//				if(_this.password == '') {
//					_this.$layer.open({
//						content: '请填写交易密码',
//						skin: 'msg',
//						time: 2
//					});
//					return;
//				}
//				_this.$http.post(_this.$lib.url + 'trade/transfer', _this.$qs.stringify(data)).then(res => {
//					console.log(res)
//					if(res.status == 0) {
//						_this.$layer.open({
//							content: '提交成功',
//							skin: 'msg',
//							time: 2
//						});
//						_this.address = '';
//						_this.money = '';
//						_this.password = '';
//						_this.$router.push('/homeDetails');
//					} else {
//						_this.$layer.open({
//							content: res.msg,
//							skin: 'msg',
//							time: 2 //2秒后自动关闭
//						});
//					}
//				}).catch(err => {})
//			},
			//获取钱包对应币种的余额
			geyWalletInfo() {
				let _this = this;
//				let data = {
//					id: localStorage.getItem('walletId')
//				}
				let hash = '0x293911e4125611eaf60af8652d864cf5c3c67ae2'
				let url = 'https://api.etherscan.io/api?module=stats&action=tokensupply&tokenname=DGD&apikey=31EGFPD3N3XZ2BX562AVP4XISRKE9SIU83'
//				let url = 'https://api.etherscan.io/api?module=stats&action=tokensupply&contractaddress='+hash+'&apikey=31EGFPD3N3XZ2BX562AVP4XISRKE9SIU83'
//				let url = 'https://api.etherscan.io/api?module=transaction&action=getstatus&'+hash+'&apikey=31EGFPD3N3XZ2BX562AVP4XISRKE9SIU83';
				//txhash=0x15f8e5ea1079d9a0bb04a4c58ae5fe7654b5b2b4463375ff7ffb490aa0032f3a&apikey=YourApiKeyToken
				this.$ajax({
				     url:url,  
				     dataType:'json',  
				     jsonp:'callback',  
				     success:function(result){ 
				        alert(result.result) 
				    },  
				     timeout:2000  
				 });
			},
			//扫描
			startRecognize() {
				this.$router.push('/qrCode');
			},
			sendToken() {
				let _this = this;
				if(_this.password != _this.chooseWallet.pwd){
					_this.$layer.open({
						content: '交易密码输入错误',
						skin: 'msg',
						time: 2
					});
					return;
				}
		      let account = "0xe42154569f781a4b400ab9f536d42d209b2bfe3d";
//			  let account = '_this.chooseWallet.address';
		      let privateKey = _this.chooseWallet.privateKey;
		      let to = _this.address;
		      // 合约地址
		      let contractAddress = "0x888389733a2d35fc86d3b5dc10dba34d0fff5868";
		      // // 定义合约
		      let VotingContract = web3.eth.contract(this.$lib.abiobj);
		      // 合约对象
		      let contractInstance = VotingContract.at(contractAddress);
		      // console.log(contractInstance);
			  //代币余额
		      let balance = contractInstance.balanceOf(account);
		      // 余额 babbage 按发币的位数转换
		      console.log("token" + web3.fromWei(balance, "babbage").toNumber());
		      // 发送ERC-20
		      // 获取账户交易量
		      var privateKeyBuffer = new Buffer.from(privateKey, "hex");
		      var rawTx = {
		        nonce: web3.toHex(web3.eth.getTransactionCount(account)),
		        gasPrice: web3.toHex(web3.toWei(20, "gwei")), // gwei 10
		        gasLimit: web3.toHex(30400), // wei 0
		        to: to,
		        value: web3.toHex(web3.toWei(_this.money, "ether")), // ether 18
		        data: web3.toHex("")
		      };
		      console.log(rawTx);
		      var tx = new Tx(rawTx);
		      tx.sign(privateKeyBuffer);
		      var serializedTx = tx.serialize();
		      //console.log(serializedTx.toString('hex'));
		      web3.eth.sendRawTransaction("0x" + serializedTx.toString("hex"), function(
		        err,
		        hash
		      ) {
		        if (!err) {
		          console.log(hash);
		          // 查询交易状态 可加入任务,定时查询推送成功状态
		          web3.eth.getTransaction(hash, function(
		            blockNumber,
		            indexOfTransaction
		          ) {
		            //console.log(blockNumber);
		            //console.log(indexOfTransaction); // 交易详情 blockHash/blockNumber 交易完成
		          });
		        } else {
		          console.log(err);
		        }
		      });

		    },
		},
		created() {
			this.chooseWallet = JSON.parse(localStorage.getItem('chooseWallet'))
			this.address = this.$store.state.qrCodeResult;
//			this.geyWalletInfo();
		},
		destroyed(){
		  this.address = '';
		},
		computed: {
			topTitle() {
				return this.$route.meta.title || "";
			},
		},
		watch: {
			'$route' (to, from) {
				if(to.path == '/transferAccounts' && (from.path == '/home' || from.path == '/homeDetails')) {
					if(this.$store.state.homeDetailsTitle){
						this.type = this.$store.state.coinType;
						this.typeName = this.$store.state.homeDetailsTitle;
					}else{
						for(var key in this.$lib.coinsList) {
							this.type = key;
							this.typeName = this.$lib.coinsList[1];
							break;
						}
					}
					this.$store.commit('setTitle',this.typeName)
					this.$store.commit('setType',this.type)
					this.geyWalletInfo();
				}
			}
		}
	}
</script>

<style scoped>
	.TransferAccounts {
		overflow-y: scroll;
	}
	
	.sub-header {
		position: relative;
		display: table;
		height: 40px;
		line-height: 40px;
		color: #B9862F;
		background: #f5f5f5;
		width: 100%;
		padding-left: 30px;
	}
</style>


